import React, { Component } from "react";
import axios from "axios";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import validationTerimaSupplier from "../../Validations/validationTerimaSupplier";
import { createNumberMask, createTextMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;

class FormTransaksiTimbangbarang extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      status: "",
    };
  }

  componentDidMount() {
    axios
      .get(server + "subcategorys")
      .then((res) =>
        this.setState({
          kodeBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  onclick1() {
    this.props.change("murni", this.props.murni);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4">
            <label htmlFor="">Kode Barang</label>
            <Field
              name="kodeBarang"
              component="select"
              className="form-control"
            >
              {this.state.kodeBarang === [] ? (
                <option>TIDAK ADA DATA</option>
              ) : (
                this.state.kodeBarang.map((kode) => (
                  <option value={kode.kodeJenis}>{kode.namaJenis}</option>
                ))
              )}
            </Field>
          </div>
          <div className="col-md-8">
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-3">
            <Field
              name="kadar"
              label="Kadar (%)"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3">
            <Field
              name="bungkus"
              label="Bungkus (Gr)"
              type="number"
              component={renderField}
            />
          </div>
          <div className="col-md-3">
            <Field
              name="berat"
              label="Berat (Gr)"
              type="number"
              component={renderField}
              onBlur={() => this.props.change("murni", this.props.murni)}
            />
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Harga Jual</label>
            <Field
              name="harga"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Revaluasi</label>
            <Field
              name="revaluasi"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-12 form-group mt-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                disabled={this.props.submitting}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTransaksiTimbangbarang = reduxForm({
  form: "TransaksiTimbangBarang",
  validate: validationTerimaSupplier,
  enableReinitialize: true,
})(FormTransaksiTimbangbarang);
export default connect()(FormTransaksiTimbangbarang);
