import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validaitonTimbangBarang from "../Validations/validationTimbangBarang";
import FormTranskaksiTimbangBarang from "./Component/FormTranskaksiTimbangBarang";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class TransaksiTimbangBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleSubmit(data) {
    console.log(data);
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="col-lg-12 col-md-12">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="tanggalTransaksi"
                    label="Tanggal"
                    type="date"
                    component={renderField}
                    normalize={(value) => value && value.toUpperCase()}
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="namaToko"
                    label="Nama Toko"
                    type="text"
                    component={renderField}
                    normalize={(value) => value && value.toUpperCase()}
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <Field
                    name="detial"
                    label="Detail"
                    type="text"
                    component={renderField}
                    normalize={(value) => value && value.toUpperCase()}
                  />
                </div>
                <div className="col-lg-12 col-md-12 mt-3">
                  <div className="row">
                    <div className="col-md-6">
                      <div className="text-left">
                        <button className="btn btn-primary mr-3">Simpan</button>
                        <button className="btn btn-primary">Batal</button>
                      </div>
                    </div>
                    <div className="col-md-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          data-target="#primary-header-modal"
                          data-toggle="modal"
                        >
                          Tambah Data
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Transaksi Timbang Barang
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormTranskaksiTimbangBarang
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

TransaksiTimbangBarang = reduxForm({
  form: "TransaksiTimbangBarang",
  enableReinitialize: true,
  validate: validaitonTimbangBarang,
})(TransaksiTimbangBarang);
export default connect()(TransaksiTimbangBarang);
